const scrollTop = document.querySelector(".scroll-top");

if (scrollTop) {
  function toggleScrollTopButton() {
    if (window.scrollY > 600) {
      scrollTop.style.opacity = "1";
      scrollTop.style.pointerEvents = "auto";
    } else {
      scrollTop.style.opacity = "0";
      scrollTop.style.pointerEvents = "none";
    }
  }

  toggleScrollTopButton();
  window.addEventListener("scroll", toggleScrollTopButton);

  scrollTop.addEventListener("click", () => {
    window.scrollTo({ top: 0, behavior: "smooth" });
  });
}

let scrollWidthFunc = () => {
  let scrollWidth = window.innerWidth - document.body.clientWidth;
  document.querySelector("html").style.paddingRight = scrollWidth + "px";
  if (document.querySelector("header").classList.contains("fixed")) {
    document.querySelector("header").style.paddingRight = scrollWidth + "px";
  }
};

document.addEventListener("DOMContentLoaded", function () {

  /* tabs */
  const tabSections = document.querySelectorAll('.tabs-section');

  tabSections.forEach((section) => {
    initTabs(section);
  });

  function initTabs(section) {
    const tabs = section.querySelectorAll('.tab');
    const tabContents = section.querySelectorAll('.tab-content');
    const tabsMobile = section.querySelector(".tabs-mobile");
    const buttonMore = section.querySelector('.button--more');
    const articlesContainer = section.querySelector('.articles__list');
    const articles = section.querySelectorAll('.article');
    const fotoContainer = section.querySelector('.gallery-page__list');
    const fotos = section.querySelectorAll('.gallery__slide');
    const reviewSection = document.querySelector('.reviews__content');
    let reviewsContainer = document.querySelector('.reviews__content .tab-content.active');
    let itemsShown = 0;
    let sortedItems = [];

    // Открываем мобильные табы
    if (tabsMobile) {
      tabsMobile.addEventListener('click', () => {
        tabsMobile.classList.toggle("opened");
      });
    }

    // Обработчик на клик по табу
    if (tabs.length > 0) {
      for (let i = 0; i < tabs.length; i++) {
        tabs[i].addEventListener('click', (event) => {

          const category = tabs[i].dataset.category;

          if (!tabs[i].classList.contains('active')) {
            tabs.forEach(tab => {
              tab.classList.remove('active');
            });
            tabs[i].classList.add('active');

            // Обнуляем переменные при переключении таба
            itemsShown = 0;
            sortedItems = [];

            // Если контейнров нет, сортируем; если есть, просто переключаем табы
            if (tabContents.length === 0) {
              if (fotoContainer) sortingContent(category, fotos, fotos.length);
            } else {
              openContent(category, tabContents);

              // Когда есть только табы и кнопка Показать ещё (берём элементы из активного таба)
              if (reviewSection) {
                reviewsContainer = tabContents[i];
                openItems(reviewsContainer.querySelectorAll('.review'), 9);
              }
            }
          }

          // Подставляем значение выбранного таба на мобилке
          if (window.matchMedia('(max-width: 768px)').matches) {
            tabsMobile.querySelector('span').textContent = tabs[i].dataset.name;
          }
        });
      }
    }

    // Переключаем контент табов
    function openContent(category, contents) {
      contents.forEach(content => {
        content.classList.remove('active');

        if (category == content.dataset.category) {
          content.classList.add('active');
        }
      })
    }

    // Сортируем элементы
    function sortingContent(data, items, step) {
      itemsShown = 0;
      sortedItems = [];
      items.forEach(item => item.classList.remove('active'));

      if (data == 'all') {
        sortedItems = Array.from(items);
      } else {
        sortedItems = Array.from(items).filter(item =>
          item.dataset.category === data
        );
      }

      for (let i = 0; i < Math.min(step, sortedItems.length); i++) {
        sortedItems[i].classList.add('active');
        itemsShown++
      }

      if (buttonMore) {
        if (itemsShown < step && !buttonMore.classList.contains('hidden')) {
          buttonMore.classList.add('hidden');
        } else if (itemsShown >= step && buttonMore.classList.contains('hidden')) {
          buttonMore.classList.remove('hidden');
        }
      }
    }

    // Обработчик клика по кнопке, определяем открывать или закрывать
    if (buttonMore) {
      buttonMore.addEventListener('click', () => {
        if (buttonMore.textContent === 'Смотреть еще') {
          if (articlesContainer) openItems(articles, 3);
          if (reviewSection) openItems(reviewsContainer.querySelectorAll('.review'), 9);
        } else {
          if (articlesContainer) closeItems(articles, 12);
          if (reviewSection) closeItems(reviewsContainer.querySelectorAll('.review'), 9);
        }
      });
    }

    // Открываем элементы
    function openItems(items, step) {
      if (sortedItems.length > 0) items = sortedItems;
      const allItems = items.length;

      if (allItems <= step) {
        buttonMore.classList.add('hidden');
      } else {
        buttonMore.classList.remove('hidden');
        buttonMore.textContent = 'Смотреть еще';
      }

      for (let i = itemsShown; i < Math.min(itemsShown + step, allItems); i++) {
        items[i].classList.add('active');
      }

      itemsShown = Math.min(itemsShown + step, allItems);

      if (itemsShown == allItems) {
        buttonMore.textContent = 'Свернуть';
      }
    }

    // Скрываем элементы
    function closeItems(items, step) {
      if (sortedItems.length > 0) items = sortedItems;
      itemsShown = 0;
      items.forEach(item => item.classList.remove('active'));

      // Показываем минимум, обновляем кнопку, прокручиваем страницу вверх
      openItems(items, step);
      buttonMore.textContent = 'Смотреть еще';
      window.scrollTo({ top: 0, behavior: "smooth" });
    }

    // Показываем первые элементы при загрузке страницы
    if (articlesContainer) openItems(articles, 12);
    if (reviewSection) openItems(reviewsContainer.querySelectorAll('.review'), 9);
  }

  /* end tabs */

  Fancybox.bind("[data-fancybox]", {
    loop: true,
  });

  // Меню

  const body = document.body;
  const burger = document.querySelector(".header__burger");
  const headerMenu = document.querySelector(".menu-mobile");

  const toggleMainMenu = (event) => {
    event.preventDefault();
    const link = event.currentTarget;
    const item = link.closest('.main-navigation__item');
    item.classList.toggle("opened");
  };

  const toggleSubMenu = (event) => {
    event.preventDefault();
    const sublink = event.currentTarget;
    sublink.classList.toggle("opened");
  };

  const onSubmenuListener = function () {
    const links = document.querySelectorAll(".main-navigation__link");
    links.forEach((link) => {
      link.addEventListener("click", toggleMainMenu);
    });

    const sublinks = document.querySelectorAll(".submenu__link");
    sublinks.forEach((sublink) => {
      sublink.addEventListener("click", toggleSubMenu);
    });
  };

  const offSubmenuListener = function () {
    const links = document.querySelectorAll(".main-navigation__link");
    links.forEach((link) => {
      link.removeEventListener("click", toggleMainMenu);
    });

    const sublinks = document.querySelectorAll(".submenu__link");
    sublinks.forEach((sublink) => {
      sublink.removeEventListener("click", toggleSubMenu);
    });
  };

  const onButtonClick = function () {
    if (burger.classList.contains("header__burger--opened")) {
      headerMenu.classList.remove("opened");
      burger.classList.remove("header__burger--opened");
      body.classList.remove("no-scroll");
      offSubmenuListener();
    } else {
      headerMenu.classList.add("opened");
      burger.classList.add("header__burger--opened");
      body.classList.add("no-scroll");
      onSubmenuListener();
    }
  };

  burger.addEventListener("click", onButtonClick);

  // Popups
  function popupClose(popupActive) {
    popupActive.classList.remove("open");
    document.body.classList.remove("no-scroll");
    document.querySelector("html").removeAttribute("style");
    document.querySelector("header").removeAttribute("style");
  }
  const popupOpenBtns = document.querySelectorAll(".popup-btn");
  const popups = document.querySelectorAll(".popup");
  const closePopupBtns = document.querySelectorAll(".popup__close");
  const buttonBack = document.querySelector(".button-back");
  const popupBtnsOk = document.querySelectorAll(".button__ok");
  closePopupBtns.forEach(function (el) {
    el.addEventListener("click", function (e) {
      popupClose(e.target.closest(".popup"));
    });
  });
  buttonBack.addEventListener("click", function (e) {
    popupClose(e.target.closest(".popup"));
  });
  popupBtnsOk.forEach(function (el) {
    el.addEventListener("click", function (e) {
      popupClose(e.target.closest(".popup"));
    });
  });
  popupOpenBtns.forEach(function (el) {
    el.addEventListener("click", function (e) {
      e.preventDefault();
      const path = e.currentTarget.dataset.path;
      const currentPopup = document.querySelector(`[data-target="${path}"]`);
      if (currentPopup) {
        popups.forEach(function (popup) {
          popupClose(popup);
          popup.addEventListener("click", function (e) {
            if (!e.target.closest(".popup__content")) {
              popupClose(e.target.closest(".popup"));
            }
          });
        });
        currentPopup.classList.add("open");
        scrollWidthFunc();
        document.querySelector("body").classList.add("no-scroll");
      }
    });
  });
  /* end popups */

  /* yandex map */
  const map = document.querySelectorAll('#map');
  let mapInitialized = false;

  if (map.length > 0) {
    function onEntryMap(e) {
      e.forEach(e => {
        if (e.isIntersecting && !mapInitialized) {
          loadMap();
        }
      })
    }
    let options = {
      threshold: [0.5],
    },
      observer = new IntersectionObserver(onEntryMap, options)
    for (let e of map) observer.observe(e)
  }

  function loadMap() {
    if (!document.querySelector('[src="https://api-maps.yandex.ru/2.1/?lang=ru_RU"]')) {
      const script = document.createElement('script');
      script.type = 'text/javascript';
      script.src = 'https://api-maps.yandex.ru/2.1/?lang=ru_RU&apikey=a4f55514-66d5-4d7b-9085-a40677911f31';
      script.onload = initMap;
      document.head.appendChild(script);
    } else {
      initMap();
    }
  }

  function initMap() {
    if (mapInitialized) return;
    mapInitialized = true;

    ymaps.ready(function () {
      const myMap = new ymaps.Map('map', {
        center: [43.703880, 39.584119],
        zoom: 17,
        controls: ["smallMapDefaultSet"]
      });
      const myPlacemark = new ymaps.Placemark(
        [43.703880, 39.584119], {
        hintContent: 'г. Сочи, ул. Декабристов, 33',
        balloonContent: 'г. Сочи, ул. Декабристов, 33'
      }
      );
      myMap.geoObjects.add(myPlacemark);
      myMap.behaviors.disable(['scrollZoom']);
    });
  }
  /* end yandex map */

  // sliders

  const breakpoint = window.matchMedia('(min-width: 1024px)');

  let tariffs;
  let steps;

  const breakpointChecker = function () {
    if (breakpoint.matches === true) {
      if (tariffs !== undefined) tariffs.destroy(true, true);
      if (steps !== undefined) steps.destroy(true, true)
      return
    } else if (breakpoint.matches === false) {
      enableTariffs()
      enableSteps()
    }
  }

  const enableTariffs = function () {
    tariffs = new Swiper('.tariffs__slider', {
      watchOverflow: true,
      on: {
        resize: function () {
          tariffs.update()
        }
      },
      breakpoints: {
        0: {
          slidesPerView: 1.1,
          spaceBetween: 10,
        }
      }
    })
  }

  const enableSteps = function () {
    steps = new Swiper('.steps__slider', {
      watchOverflow: true,
      on: {
        resize: function () {
          steps.update()
        }
      },
      breakpoints: {
        0: {
          slidesPerView: 1.1,
          spaceBetween: 10,
        },
        768: {
          slidesPerView: 2.1,
          spaceBetween: 10,
        }
      }
    })
  }

  breakpointChecker()
  breakpoint.addEventListener('change', breakpointChecker)

  const doctorsSliderCheck = document.querySelector(".doctors__slider");
  if (doctorsSliderCheck) {
    const swiperDoctors = new Swiper(".doctors__slider", {
      navigation: {
        nextEl: ".doctors-slider__navigation .swiper-button-next",
        prevEl: ".doctors-slider__navigation .swiper-button-prev",
      },
      slidesPerView: 1.1,
      spaceBetween: 10,
      breakpoints: {
        1023: {
          slidesPerView: 2,
          spaceBetween: 10,
        }
      }
    });
  }

  const reviewsSliderCheck = document.querySelector(".reviews__slider");
  if (reviewsSliderCheck) {
    const swiperReviews = new Swiper(".reviews__slider", {
      navigation: {
        nextEl: ".reviews-slider__navigation .swiper-button-next",
        prevEl: ".reviews-slider__navigation .swiper-button-prev",
      },
      slidesPerView: 1.1,
      spaceBetween: 10,
      loop: false,
      breakpoints: {
        768: {
          slidesPerView: 2,
        },
      },
    });
  }

  function checkReviews() {
    document.querySelectorAll(".review").forEach((review) => {
      const textElement = review.querySelector(".review__text");
      if (!textElement) return;

      const maxHeight = window.innerWidth <= 768 ? 320 : 142;

      if (textElement.scrollHeight > maxHeight) {
        const button = review.querySelector(".review__button");
        button.classList.remove("hidden");

        textElement.style.maxHeight = maxHeight + "px";

        let isOpened = false;

        button.addEventListener("click", function () {
          if (!isOpened) {
            textElement.style.maxHeight = textElement.scrollHeight + "px";
            button.textContent = "Свернуть";
            isOpened = true;
          } else {
            textElement.style.maxHeight = maxHeight + "px";
            button.textContent = "Читать полностью";
            isOpened = false;
          }
        });
      }
    });
  }

  checkReviews();

  const promosSliderCheck = document.querySelector(".promos__slider");
  if (promosSliderCheck) {
    const swiperPromos = new Swiper(".promos__slider", {
      navigation: {
        nextEl: ".promos-slider__navigation .swiper-button-next",
        prevEl: ".promos-slider__navigation .swiper-button-prev",
      },
      slidesPerView: 1.1,
      spaceBetween: 10,
      loop: true,
      breakpoints: {
        768: {
          slidesPerView: 2,
        },
        1023: {
          slidesPerView: 3,
        },
      },
    });
  }

  const documentsSliderCheck = document.querySelector(".documents__slider");
  if (documentsSliderCheck) {
    const swiperDocuments = new Swiper(".documents__slider", {
      navigation: {
        nextEl: ".documents-slider__navigation .swiper-button-next",
        prevEl: ".documents-slider__navigation .swiper-button-prev",
      },
      slidesPerView: 1.1,
      spaceBetween: 10,
      loop: true,
      breakpoints: {
        768: {
          slidesPerView: 2,
        },
        1023: {
          slidesPerView: 3,
        },
      },
    });
  }

  // end sliders

  // Поиск по сайту

  const searchForms = document.querySelectorAll(".search");
  const searchBtn = document.querySelector(".search-btn");
  const searchDesktop = document.querySelector(".search--desktop");
  const popupCities = document.querySelector('.popup__bottom');

  searchForms.forEach(searchForm => {
    const input = searchForm.querySelector(".search__input");
    const results = searchForm.querySelector(".search__wrapper");

    input.addEventListener("input", () => {
      if (input.value.trim() !== "") {
        results.classList.add("active");
        if (popupCities) {
          popupCities.classList.add('hidden');
        }
      } else {
        results.classList.remove("active");
        if (popupCities) {
          popupCities.classList.remove('hidden');
        }
      }
    });

    searchBtn.addEventListener("click", () => {
      searchDesktop.classList.add("active");
      if (window.innerWidth >= 1024) {
        body.classList.add("no-scroll");
      }
    });

    document.addEventListener('click', (evt) => {
      const isActive = searchDesktop.classList.contains('active');

      if (isActive && !evt.target.closest('.search--desktop') && !evt.target.closest('.search-btn')) {
        searchDesktop.classList.remove("active");
        if (window.innerWidth >= 1024) {
          body.classList.remove("no-scroll");
        }
      };
    });
  })


  // Поиск по вопросам

  const faqSearch = document.querySelector('.intro__search');

  if (faqSearch) {
    const searchInput = document.getElementById('faq-search');
    const searchWrapper = faqSearch.querySelector('.search__wrapper');
    const searchList = faqSearch.querySelector('.search__list');
    const errorWrapper = faqSearch.querySelector('.search__wrapper--error');

    if (!searchInput || !searchWrapper || !searchList || !errorWrapper) return;

    // Отменяем отправку формы
    faqSearch.addEventListener('submit', (evt) => {
      evt.preventDefault();
    });

    // Переключаем табы
    function activateTab(category) {
      document.querySelectorAll('.main-tab').forEach(tab => tab.classList.remove('active'));
      document.querySelectorAll('.tab-content').forEach(content => content.classList.remove('active'));

      document.querySelector(`.main-tab[data-category="${category}"]`).classList.add('active');
      document.querySelector(`.tab-content[data-category="${category}"]`).classList.add('active');
    }

    // Открываем вопрос
    function openQuestion(question) {
      question.setAttribute('open', '');
      setTimeout(() => {
        question.scrollIntoView({ behavior: 'smooth', block: 'center' });
      }, 100);
    }

    // Поиск при вводе
    searchInput.addEventListener('input', function () {
      const searchValue = this.value.toLowerCase().trim();

      if (!searchValue) {
        searchWrapper.classList.remove('active');
        errorWrapper.classList.remove('active');
        return;
      }

      const allQuestions = document.querySelectorAll('.details');
      const results = [];

      allQuestions.forEach(question => {
        const questionText = question.querySelector('.details__title span:first-child').textContent;
        const answerText = question.querySelector('.details__content').textContent;
        const category = question.closest('.tab-content').dataset.category;

        // Сохраняем вопрос
        if (questionText.toLowerCase().includes(searchValue) || answerText.toLowerCase().includes(searchValue)) {
          results.push({
            element: question,
            question: questionText,
            category: category
          });
        }
      });

      searchList.innerHTML = '';

      if (results.length) {
        searchWrapper.classList.add('active');
        errorWrapper.classList.remove('active');

        results.forEach(result => {
          const listItem = document.createElement('li');
          listItem.textContent = result.question;

          listItem.addEventListener('click', () => {
            activateTab(result.category);

            if (result.element) {
              openQuestion(result.element);
            }
          });

          searchList.appendChild(listItem);
        });
      } else {
        searchWrapper.classList.remove('active');
        errorWrapper.classList.add('active');
      }
    });

    // Закрываем список при клике снаружи
    document.addEventListener('click', function (event) {
      if (!event.target.closest('.intro__search')) {
        searchWrapper.classList.remove('active');
        errorWrapper.classList.remove('active');
      }
    });
  }

  // Содержание
  const textNavigation = document.querySelector('.text-content__nav');
  if (textNavigation) {

    const headers = document.querySelectorAll('h2');

    if (headers.length > 0) {
      const textNavigationList = textNavigation.querySelector('ul');
      textNavigationList.innerHTML = '';

      for (let i = 0; i < headers.length; i += 1) {
        const header = headers[i];
        const titleBlock = header.textContent;
        const textNavigationItem = document.createElement('li');
        const textNavigationLink = document.createElement('a');

        // Присваиваем ID
        const id = 'top-' + (i + 1);
        header.setAttribute('id', id);

        textNavigationLink.setAttribute('href', `#${id}`);
        textNavigationLink.textContent = titleBlock;
        textNavigationItem.append(textNavigationLink);
        textNavigationList.append(textNavigationItem);
      }

      document.querySelectorAll('a[href^="#"]').forEach(link => {
        link.addEventListener('click', function (e) {
          e.preventDefault();
          const href = this.getAttribute('href').substring(1);
          const scrollTarget = document.getElementById(href);

          if (scrollTarget) {
            const topOffset = 280;
            const elementPosition = scrollTarget.getBoundingClientRect().top;
            const offsetPosition = elementPosition - topOffset;

            window.scrollBy({
              top: offsetPosition,
              behavior: 'smooth'
            });
          }
        });
      });
    } else {
      // Если заголовков нет, скрываем навигацию
      const details = textNavigation.closest('details');
      if (details) {
        details.style.display = 'none';
      }
    }
  }

  // Раскрытие текстовых блоков
  const openButtons = document.querySelectorAll('.button--open');

  openButtons.forEach(button => {
    button.addEventListener('click', function () {
      const textContent = this.closest('.text-content');

      if (textContent) {
        const wrapper = textContent.querySelector('.text-content__wrapper');

        if (wrapper) {
          // Определяем значения в зависимости от ширины экрана
          const isMobile = window.innerWidth <= 768;
          const closedHeight = isMobile ? '1610px' : '750px';
          const openedHeight = isMobile ? '7500px' : '5500px';

          if (wrapper.style.maxHeight === openedHeight) {
            wrapper.style.maxHeight = closedHeight;
            this.textContent = 'Читать полностью';
            this.classList.remove('opened');
          } else {
            wrapper.style.maxHeight = openedHeight;
            this.textContent = 'Свернуть';
            this.classList.add('opened');
          }
        }
      }
    });
  });

  // Раскрытие списка
  function openList(block, step, buttonText) {
    const moreButtons = block.querySelectorAll('.button__more');

    moreButtons.forEach(button => {
      button.addEventListener('click', function () {
        const list = this.closest('ul') || this.previousElementSibling;
        if (!list || list.tagName !== 'UL') {
          return;
        }

        const elements = list.querySelectorAll('li');

        if (elements.length > 0) {
          for (let i = step; i < elements.length; i++) {
            elements[i].classList.toggle('hidden');
          }

          button.textContent = button.textContent === buttonText ? 'Свернуть' : buttonText;
        }
      })
    });
  }

  const tariffBlock = document.querySelector('.tariffs');
  if (tariffBlock) openList(tariffBlock, 6, 'Все преимущества');

  const vacancies = document.querySelector('.vacancies');
  if (vacancies) openList(vacancies, 3, 'Все требования');

  //  Анимация

  const animationItems = document.querySelectorAll(".animation-item");
  if (animationItems.length > 0) {
    function onEntry(e) {
      e.forEach((e) => {
        e.isIntersecting && e.target.classList.add("animation-active");
      });
    }
    let options = {
      threshold: [0.5],
    },
      observer = new IntersectionObserver(onEntry, options);
    for (let e of animationItems) observer.observe(e);
  }

  // Куки

  if (Number(localStorage.getItem("cookie-checker")) != 1) {
    const infoAlert = document.createElement("div");
    const okBtn = document.createElement("button");
    okBtn.innerText = "Принять";
    infoAlert.classList.add("cookie-block");
    infoAlert.innerHTML =
      '<p>Этот сайт использует файлы cookies для более комфортной работы пользователя. К сайту подключен сервис Яндекс.Метрика. Продолжая просмотр страниц сайта, вы соглашаетесь на обработку персональных данных в соответствии с <a href="/politika-obrabotki-i-zashchity-personalnyh-dannyh/">Политикой обработки и защиты персональных данных</a>, <a href="/polzovatelskoe-soglashenie/">Пользовательским соглашением</a>, <a href="/soglasie-na-obrabotku-personalnyh-dannyh-yandexmetrika/">Согласием на обработку персональных данных с помощью Яндекс.Метрика</a>.</p>';
    infoAlert.appendChild(okBtn);

    okBtn.addEventListener("click", function (e) {
      e.preventDefault();
      localStorage.setItem("cookie-checker", "1");
      infoAlert.classList.add("accepted");
      setTimeout(function () {
        document.querySelector(".cookie-block").remove();
      }, 1000);
    });

    document.body.appendChild(infoAlert);
  }

  // Калькулятор

  let answers = {};
  const treatmentFormats = {
    'emergency': {
      title: 'Экстренная детоксикация + амбулаторное лечение',
      match: '75-85%',
      description: 'Рекомендуется начинать с экстренной помощи для снятия острых симптомов с последующим переходом на амбулаторную программу.',
      recommendations: [
        'Вывод из запоя/снятие ломки на дому или в стационаре',
        'Детоксикационная терапия под наблюдением врача',
        'Амбулаторное лечение с регулярными визитами к наркологу',
        'Медикаментозная поддержка для снижения тяги',
        'Психологическое консультирование 1-2 раза в неделю'
      ],
      services: ['Лечение алкоголизма', 'Лечение наркомании']
    },
    outpatient: {
      title: 'Амбулаторное лечение с поддержкой',
      match: '80-90%',
      description: 'Программа для тех, кто может сохранять повседневную активность, но нуждается в систематической профессиональной поддержке.',
      recommendations: [
        'Регулярные визиты к наркологу (2-4 раза в месяц)',
        'Индивидуальная психотерапия по программе 12 шагов',
        'Групповые занятия поддержки',
        'Медикаментозное сопровождение',
        'Работа с психологом над причинами зависимости',
        'Семейная терапия при наличии поддержки близких'
      ],
      services: ['Лечение алкоголизма', 'Лечение наркомании', 'Психотерапия']
    },
    'comprehensive': {
      title: 'Комплексный стационар',
      match: '85-95%',
      description: 'Наиболее эффективный вариант при средней и тяжелой степени зависимости. Полная изоляция от триггеров и круглосуточная поддержка.',
      recommendations: [
        'Госпитализация в стационар на 14-30 дней',
        'Круглосуточное наблюдение врачей и психологов',
        'Интенсивная детоксикация и восстановительная терапия',
        'Ежедневные индивидуальные и групповые занятия',
        'Семейная терапия и работа с созависимостью',
        'Физиотерапия и ЛФК для восстановления организма'
      ],
      services: ['Лечение алкоголизма', 'Лечение наркомании', 'Стационарное лечение']
    },
    'coding': {
      title: 'Кодирование + реабилитация',
      match: '70-85%',
      description: 'Подходит для тех, кто нуждается в быстром результате и дополнительной мотивации с последующей психологической работой.',
      recommendations: [
        'Медикаментозное или психотерапевтическое кодирование',
        'Курс поддерживающей терапии после кодирования',
        'Реабилитационная программа 3-6 месяцев',
        'Работа с психологом над изменением мышления',
        'Поддерживающие группы после основного лечения',
        'Профилактика рецидивов'
      ],
      services: ['Кодирование', 'Реабилитация', 'Лечение алкоголизма']
    },
    'rehabilitation': {
      title: 'Долгосрочная реабилитация',
      match: '90-95%',
      description: 'Программа для достижения устойчивой ремиссии с полным изменением образа жизни и мышления.',
      recommendations: [
        'Реабилитационный центр на 3-12 месяцев',
        'Полное погружение в терапевтическую среду',
        'Интенсивная психологическая работа',
        'Трудовая терапия и социальная адаптация',
        'Работа с родственниками и восстановление отношений',
        'Программа профилактики рецидивов',
        'Поддержка после выписки (амбулаторные группы)'
      ],
      services: ['Реабилитация', 'Социальная адаптация', 'Долгосрочная поддержка']
    }
  };

  // Текущий шаг
  let currentSlide = 1;

  // Инициализация
  updateProgress();

  // Обработка выбора ответов
  document.querySelectorAll('.answer').forEach(answer => {
    answer.addEventListener('click', function () {
      // Находим радио-кнопку внутри и отмечаем её
      const radio = this.querySelector('input[type=radio]');
      if (radio) {
        radio.checked = true;

        const slide = this.closest('.slide');
        const slideId = slide.id.replace('slide', '');

        // Сохраняем ответ
        answers['slide' + slideId] = parseInt(this.getAttribute('data-value'));
      }
    });
  });

  // Переход к следующему шагу
  function nextSlide() {
    // Проверяем, выбран ли ответ на текущем шаге
    if (!answers['slide' + currentSlide]) {
      alert('Пожалуйста, выберите ответ на вопрос');
      return;
    }

    // Если это был последний вопрос - показываем результат
    if (currentSlide === 5) {
      document.querySelectorAll('.calculator__navigation').forEach(nav => {
        nav.classList.add('hidden');
      });
      calculateResult();
      return;
    }

    // Скрываем текущий шаг
    document.getElementById('slide' + currentSlide).classList.remove('active');

    // Переходим к следующему
    currentSlide++;
    document.getElementById('slide' + currentSlide).classList.add('active');

    updateProgress();
  }

  const nextBtns = document.querySelectorAll('.calculator__navigation .button');
  nextBtns.forEach(btn => {
    btn.addEventListener('click', () => {
      nextSlide();
    })
  })

  // Обновление прогресса
  function updateProgress() {
    document.querySelectorAll('.calculator__slide').forEach(pagination => {
      pagination.textContent = currentSlide;
    })
  }

  // Расчет результата
  function calculateResult() {
    if (!answers['slide5']) {
      alert('Пожалуйста, выберите ответ на последний вопрос');
      return;
    }

    // Подсчет суммы баллов
    let totalScore = 0;
    for (let key in answers) {
      totalScore += answers[key];
    }

    // Определение формата лечения
    let treatmentFormat;

    if (totalScore <= 8) {
      treatmentFormat = 'coding'; // Легкая зависимость
    } else if (totalScore <= 12) {
      treatmentFormat = 'outpatient'; // Средняя зависимость
    } else if (totalScore <= 16) {
      treatmentFormat = 'comprehensive'; // Тяжелая зависимость
    } else if (answers['slide5'] === 1) {
      treatmentFormat = 'emergency'; // Нужна срочная помощь
    } else {
      treatmentFormat = 'rehabilitation'; // Долгосрочное лечение
    }

    // Показываем результат
    const format = treatmentFormats[treatmentFormat];
    document.getElementById('slide5').classList.remove('active');
    document.getElementById('slideResult').classList.add('active');

    document.getElementById('resultType').textContent = format.title;
    document.getElementById('resultMatch').textContent = format.match + ' совпадение';
    document.getElementById('resultDescription').textContent = format.description;

    // Заполняем рекомендации
    const list = document.getElementById('recommendationList');
    list.innerHTML = '';
    format.recommendations.forEach(rec => {
      const li = document.createElement('li');
      li.innerHTML = `${rec}`;
      list.appendChild(li);
    });

    document.querySelector('.calculator__navigation').style.display = "none";
  }

  // Виджет

  const widget = document.querySelector(".widget");
  const openBtn = document.querySelector(".widget__open");
  const wrapper = document.querySelector(".widget__wrapper");

  if (!widget) return;

  openBtn.addEventListener("click", function (event) {
    wrapper.classList.toggle("active");
    openBtn.classList.toggle("active");
  });

  const widgetLinks = widget.querySelectorAll(".widget__link");
  widgetLinks.forEach((link) => {
    link.addEventListener("click", function () {
      wrapper.classList.remove("active");
      openBtn.classList.remove("active");
    });
  });

})
